.footer {
  grid-area: f;
  background-color: rgb(0, 0, 0);
  grid-gap: 10px;
  margin: 5px 10px 10px 10px;
  height: 15vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  font-size: 24px;
  color: lightgreen;
}

.main {
  /*grid-gap: 5px;*/
  /*margin: 10px 10% 10px 10%;*/
  width: 80%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  color: lightgreen;
  height: 80%;
  border: 1px solid;
}
.icon {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  /*margin: 10px;*/
  background-color: darkgray;
  border: 1px solid;
  height: 80%;
  width: 60%;
}

.medium
{
  display: flex;
  height: 100px;
  width: 850px;
  text-align: center;
  /*border-radius: 50%;*/
  /*margin: 5px;*/
  background-color: black;
  color: white;
  justify-content: center;
  align-items: center;
  border: 1px solid;
}