.works {
  grid-area: w;
  background-color: rgb(15%, 15%, 15%);
  /*grid-gap: 10px;*/
  /*margin: 10px 10% 10px 10%;*/
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  font-size: 24px;
  color: lightgreen;
  height: 100vh;
}
.main {
  /*grid-gap: 5px;*/
  /*margin: 10px 10% 10px 10%;*/
  /*width: 80%;*/
  width: 80%;
  height: 100vh;
  /*height:80%;*/
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  font-size: 24px;
  color: lightgreen;
  border: 1px solid;
}

.row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  /*margin: 10px;*/
  background-color: darkgray;
  border: 1px solid;
  height: 90%;
  width: 100%;
}

.large {
  display: flex;
  flex-direction: column;
  height: 90%;
  width: 40%;
  font-size: 12px;
  align-items: center;
  justify-content: flex-end;
  text-align: center;
  margin: 20px;
  background-color: rgb(20%, 20%, 20%);
  color: cyan;
  border: 1px solid;
}

.medium {
  display: flex;
  height: 40%;
  width: 100%;
  text-align: center;
  flex-direction: column;
  /*border-radius: 50%;*/
  margin: 5px;
  background-color: black;
  color: white;
  justify-content: center;
  align-items: center;
  /*order: 4;*/
  font-weight: bold;
}

.watch {
  display: flex;
  height: 10%;
  width: 30%;
  text-align: center;
  /*border-radius: 50%;*/
  background-color: saddlebrown;
  color: white;
  justify-content: center;
  align-items: center;
  /*order: 1;*/
  align-self: center;
  margin: auto;
  /*padding: 50px 50px 50px 50px;*/
}

.small {
  display: flex;
  height: 30%;
  width: 80%;
  text-align: center;
  /*border-radius: 50%;*/
  background-color: brown;
  color: white;
  justify-content: center;
  align-items: center;
  margin: auto;
  /*order: 3;*/
}

.img {
  display: flex;
  height: 400px;
  width: 650px;
  text-align: right;
  /*border-radius: 50%;*/

  background-color: slateblue;
  justify-content: center;
  align-items: center;
  /*order: 2;*/
}