.freelance {
  grid-area: fr;
  background-color: rgb(10%, 10%, 10%);
  grid-gap: 10px;
  /*margin: 10px 10% 10px 10%;*/
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  font-size: 24px;
  color: lightgreen;
  height: 30vh;
}

.main {
  /*grid-gap: 5px;*/
  /*margin: 10px 10% 10px 10%;*/
  /*width: 80%;*/
  width: 80%;
  /*height:80%;*/
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  font-size: 24px;
  color: lightgreen;
  height: 100%;
  border: 1px solid;
}

.row {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  /*margin: 10px;*/
  background-color: darkgray;
  border: 1px solid;
  height: 90%;
  width: 100%;
}

.medium
{
  display: flex;
  height: 100px;
  width: 850px;
  text-align: center;
  /*border-radius: 50%;*/
  /*margin: 5px;*/
  background-color: black;
  color: white;
  justify-content: center;
  align-items: center;
  border: 1px solid;
}

